<template>
	<div id="service-order-list">
		<public-header title="售后"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-tabs v-model="filter.items.refund_type" @change="onToggleStatus" sticky offset-top="46">
				<van-tab :title="item.text" v-for="(item, index) in orderStatusArr" :key="index" :name="item.refund_type">
					<van-list v-model="orders.loading" :finished="orders.finished" @load="onLoad" :offset="100" :immediate-check="true">
						<template v-if="orders.data.length > 0">
							<template v-for="(item, index) in orders.data">
								<div class="order-item">
									<div class="info" @click="util.jsUrl('/mall/pages/order/detail', {id: item.id})">
										<div class="flex-lr">
											<div>
												<span>{{item.store.title}}</span>
											</div>
											<div>{{item.status_cn}}</div>
										</div>
										<div class="goods-list">
											<div class="goods-item" v-for="(goodsItem, goodIndex) in item.goods" :key="goodIndex">
												<div class="goods-thumb van-hairline--surround">
													<img class="img-100" :src="goodsItem.thumb" alt="">
												</div>
											</div>
										</div>
										<div class="total">
											<div class="margin-10-r font-12">
												共
												<span>{{item.goods_num}}</span>
												类商品
											</div>
											<div class="c-danger">
												<span class="font-12">{{Lang.dollarSign}}</span><span class="font-16 font-bold">{{item.final_fee}}</span>
											</div>
										</div>
									</div>
									<div class="btn-group van-hairline--top">
										<van-button plain round hairline type="default" v-clipboard:copy="item.ordersn" v-clipboard:success="onCopy" v-clipboard:error="onError">复制订单编号</van-button>
										<template v-if="item.refund_status == 0">
											<van-button @click="util.jsUrl('/mall/pages/service/refund', {id: item.id})" plain round hairline type="default">申请售后</van-button>
										</template>
										<template v-else-if="item.refund_status > 0">
											<van-button @click="util.jsUrl('/mall/pages/service/detail', {id: item.id})" plain round hairline size="normal">售后详情</van-button>
										</template>
									</div>
								</div>
							</template>
							<load type="loaded" text="我是有底线的" bgcolor="#f5f5f5" v-if="orders.finished"></load>
						</template>
						<div class="common-no-con" v-else>
							<img src= "static/img/order_no_con.png" alt="" />
							<p>暂无售后信息！</p>
						</div>
					</van-list>
				</van-tab>
			</van-tabs>
		</div>
	</div>
</template>

<script>
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import load from '@/components/load'
	import { Dialog } from 'vant';
	export default {
		components: {
			PublicHeader,
			PublicFooter,
			load,
			Dialog
		},
		data() {
			return {
				orderStatusArr: [
					{
						text: '申请售后',
						refund_type: 0
					},
					{
						text: '售后处理中',
						refund_type: 1
					},
					{
						text: '售后记录',
						refund_type: 2
					},
				],
				orders: {
					page: 1,
					psize: 10,
					finished: false,
					loading: false,
					data:[]
				},
				filter: {
					items: {
						refund_type: 0
					}
				},
				showPreLoading: true,
				menufooter: {},
			}
		},
		methods: {
			onToggleStatus(status) {
				this.filter.items.refund_type = status;
				this.onLoad(true)
			},

			onChangeStatus(oid, type) {
				if(type == 'cancel') {
					var params = {
						url: 'wmall/order/index/cancel?id=' + oid,
						confirm: '确定取消订单吗?',
					}
				} else if(type == 'end_1' || type == 'end_2') {
					var params = {
						url: 'wmall/order/index/end?id=' + oid,
						confirm: '你确定收到该商家的外卖?',
						successUrl: '/mall/pages/order/comment?order_id=' + oid,
					}
					if(type == 'end_2') {
						params.confirm = '你确定收到该商家的外卖?';
					}
				} else if(type == 'remind') {
					var params = {
						url: 'wmall/order/index/remind?id=' + oid,
					}
				}
				this.util.jspost(params);
			},
			onFinishMealPay() {
				Dialog.confirm({
					title: '温馨提示',
					message: '您的支付方式为餐后支付，请到商家收银台付款',
				}).then(() => {
					return;
				})
			},

			onLoad(force) {
				if(force) {
					this.orders = {
						page: 1,
						psize: 10,
						loading: true,
						finished: false,
						data:[]
					};
				}
				if(this.orders.finished) {
					return false;
				}
				this.util.request({
					url: 'wmall/mall/service/list',
					data: {
						page: this.orders.page,
						psize: this.orders.psize,
						refund_type: this.filter.items.refund_type,
						menufooter: 1,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.hideLoading();
					result = result.message;
					this.orders.data = [...this.orders.data, ...result.orders];
					this.orders.loading = false;
					if(result.orders.length < this.orders.psize) {
						this.orders.finished = true;
					}
					this.orders.page++;
					this.menufooter = window.menufooter;
				});
			},

			onCopy(e) {
				this.util.$toast('复制成功')
			},

			onError: function (e) {
				this.util.$toast('复制失败')
			},

			hideLoading(){
				this.showPreLoading = false;
			},
		},

		mounted() {
		}
	}
</script>

<style>
	@import "../../../../static/css/test.css";
	#service-order-list .order-item{
		margin: 10px;
		border-radius: 5px;
		background-color: #fff;
		font-size: 14px;
	}
	#service-order-list .order-item .info{
		padding: 15px;
	}
	#service-order-list .order-item .goods-list{
		display: flex;
		white-space: nowrap;
		overflow-x: scroll;
		padding-top: 15px;
	}
	#service-order-list .order-item .goods-item{
		margin-right: 10px;
	}
	#service-order-list .order-item .goods-list .goods-item:last-child{
		margin-right: 0;
	}
	#service-order-list .order-item .goods-thumb{
		width: 50px;
		height: 50px;
		border-radius: 5px;
		overflow: hidden;
	}
	#service-order-list .order-item .total{
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-end;
		justify-content: flex-end;
		padding: 15px 0;
	}
	#service-order-list .order-item .btn-group{
		display: flex;
		flex-flow: row nowrap;
		justify-content: flex-end;
		white-space: nowrap;
		overflow-x: scroll;
		padding: 10px 15px;
	}
	#service-order-list .order-item .btn-group .van-button{
		margin-left: 10px;
		height: 40px;
	}
	#service-order-list .common-no-con{
		position: fixed;
		z-index: 5;
		top: 32%;
		left: 0;
		width: 100%;
		height: 200px;
		text-align: center;
		color: #768491;
		font-size: 16px;
	}
	#service-order-list .common-no-con img{
		max-width: 25%;
		vertical-align: middle;
	}
	#service-order-list .common-no-con p{
		margin: 20px 0;
	}
	#service-order-list .common-no-con .common-no-con-a{
		display: inline-block;
		height: 35px;
		line-height: 35px;
		color: #fff;
		font-size: 14px;
		background: #ff2d4b;
		padding: 0 10px;
		border-radius: 35px;
	}
</style>


